
import wx from 'weixin-js-sdk';
import common from "@/assets/js/common"
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
uid: this.$store.state.uid,
userData: {},
money: ''
}
},
mounted() {
this.getWebUser(this.uid);

this.wxConfig()
},
components: {
AlertModule
},
methods: {
...common,

tackAll() {
this.money = this.userData.amount
},

cashWithdrawal() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
AlertModule.show({
title: '1%',
onHide: () => {
if (that.money.trim() != '' && that.money > 0) {
that.$http('post', baseUrl + 'api/FeeRecord/Withdraw', {
uid: that.uid,
amount: that.money
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "",
onHide() {
that.$router.push({
path: '/cashWithdrawalSuccess'
})
}
})

console.log('')
wx.ready(function () {
wx.chooseWXPay({
timestamp: result.timeStamp, // jssdktimestamptimeStampS
nonceStr: result.nonceStr, //  32 
package: result.package, // prepay_idprepay_id=***
signType: result.signType, // 'SHA1''MD5'
paySign: result.paySign, // 
success: function (res) {
console.log('' + res)

that.$router.push({
path: '/cashWithdrawalSuccess'
})
},
fail: (res) => {
AlertModule.show({
title: ""
})
}
});
})

}

})
} else {
AlertModule.show({
title: ""
})
}

}
})

},

wxConfig() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
url: encodeURI("http://www.shuimujiajia.net/app/index.html")
}
that
.$http("get", baseUrl + "WexinJsConfig", postData)
.then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
var result = res.data.data;
wx.config({
debug: false, // ,
appId: result.appId, // corpid
timestamp: result.timestamp, // 
nonceStr: result.nonceStr, // 
jsApiList: ["chooseWXPay"],
signature: result.signature, // 1
jsToken: result.jsToken 
});
}
})
},
}

}



